idden;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.song-request-item::before {\r\n  content: '';\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  bottom: 0;\r\n  width: 4px;\r\n  background: linear-gradient(135deg, #4ade80 0%, #22c55e 100%);\r\n  border-radius: 2px;\r\n}\r\n\r\n.song-request-item:hover {\r\n  background: linear-gradient(135deg, rgba(74, 222, 128, 0.08) 0%, rgba(74, 222, 128, 0.03) 100%);\r\n  border-color: rgba(74, 222, 128, 0.3);\r\n  transform: translateY(-4px) scale(1.02);\r\n  box-shadow: \r\n    0 8px 25px rgba(0, 0, 0, 0.2),\r\n    0 0 0 1px rgba(74, 222, 128, 0.1);\r\n}\r\n\r\n@keyframes fadeInUp {\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n.request-info {\r\n  flex: 1;\r\n  min-width: 0;\r\n}\r\n\r\n.username {\r\n  font-weight: 600;\r\n  font-size: 14px;\r\n  color: #fbbf24;\r\n  margin-bottom: 4px;\r\n  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n.song-details {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 2px;\r\n}\r\n\r\n.song-title {\r\n  font-size: 13px;\r\n  font-weight: 500;\r\n  color: white;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.artist {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.7);\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.remove-btn {\r\n  background: rgba(248, 113, 113, 0.2);\r\n  border: 1px solid rgba(248, 113, 113, 0.3);\r\n  color: #f87171;\r\n  border-radius: 50%;\r\n  width: 24px;\r\n  height: 24px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  cursor: pointer;\r\n  font-size: 16px;\r\n  font-weight: bold;\r\n  margin-left: 10px;\r\n  transition: all 0.2s ease;\r\n}\r\n\r\n.remove-btn:hover {\r\n  background: rgba(248, 113, 113, 0.3);\r\n  transform: scale(1.1);\r\n}\r\n\r\n.no-requests {\r\n  text-align: center;\r\n  padding: 40px 20px;\r\n  color: rgba(255, 255, 255, 0.7);\r\n}\r\n\r\n.no-requests p 